@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;500&display=swap);
.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  background-color: rgba(232, 241, 245, 255);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Dosis, sans-serif;
}

h1 {
  font-size: 2.5rem;
  color: #ed5874;
}

@media only screen and (min-width: 600px) {
  h1 {
    font-size: 4.5rem;
  }
}

.icon {
  margin: 0.3rem;
}

.tile-container {
  width: 100%;
  height: 100%;
}

.tile {
  border-radius: 2%;
  width: 100%;
  height: 80%;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

button {
  font-size: 1rem;
  font-family: Dosis, sans-serif;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  border: 0;
  border-radius: 5%;
  background-color: azure;
}

@media only screen and (min-width: 600px) {
  .tile-container {
    width: 50%;
  }
  .tile {
    width: 100%;
    height: 80%;
    transition: all 0.3s ease-out;
  }

  .tile:hover {
    -webkit-transform: translate(0, -5px);
            transform: translate(0, -5px);
  }
  button {
    font-size: 2rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    margin-top: 1rem;
    transition: all 0.3s ease-out;
  }

  button:hover {
    -webkit-transform: translate(0, -5px);
            transform: translate(0, -5px);
  }
}

.color-grid {
  background-color: #fce38a;
  height: 80vh;
  width: 70%;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 2%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

@media only screen and (min-width: 600px) {
  .color-grid {
    flex-direction: row;
    height: 30rem;
    width: 80%;
  }
}

.refresh-button {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.refresh-button > button {
  padding: 1rem;
  font-size: 1rem;
  border-radius: 10%;
}
@media only screen and (min-width: 600px) {
  .refresh-button > button {
    padding: 2rem;
    font-size: 2rem;
    border-radius: 10%;
    transition: all 0.3s ease-out;
  }

  .refresh-button > button:hover {
    -webkit-transform: translate(0, -5px);
            transform: translate(0, -5px);
  }
}

body {
  margin: 0;
}

